body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  nav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: lightblue;
    padding: 9px;
  }
  nav input{
    width: 250px;
    text-decoration: none;
    padding: 9px;
    font-size: 18px;
    border: none;
  }
  .searchBar{
    display: flex;
    align-items: center;
    gap: 11px;
  }
  nav button{
    width: 70px;
    height: 38px;
    background-color: blue;
    border: none;
    border-radius: 2px;
    color: white;
    font-size: 16px;
    cursor: pointer;
  }
  nav ul{
    display: flex;
    gap: 11px;
  
  }
  .cardContainer{
    display: flex;
    justify-content: center;
    gap:41px;
    flex-wrap: wrap;
  
  }
  
  .card{
    width: 320px;
    box-shadow: 0px 4px 4px 4px rgb(255, 255, 255);
    background-color: white;
    border-radius: 3px;
    margin-top: 21px;
    border: 1px solid rgb(236, 236, 236);
  }
  .card img{
    width: 100%;
    height: 180px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
  
  }
  .card .title{
    font-weight: 600;
    font-size: 18px;
    margin-top: -7px;
  }
  .card .title:hover{
    border-bottom: 1px solid black;
    cursor: pointer;
  }
  .card .content{
    padding: 9px;
  }
  .card button{
    background-color: rgb(29, 162, 214);
    border: none;
    cursor: pointer;
    width: 80px;
    height: 28px;
    color: white;
    font-size: 13px;
    border-radius: 2px;
  }
  .categoryBtn{
    display: flex;
    justify-content: center;
    gap: 11px;
  }
  .categoryBtn button{
    gap: 11px;
    width: 130px;
    height: 33px;
    background-color: rgb(255, 108, 108);
    border: none;
    outline: none;
    font-size: 18px;
    border-radius: 16px;
    color: white;
    cursor: pointer;
    padding: 2px;
  }
  
  
  .head{
    text-align: center;
    font-weight: 600;
    font-size: 26px;
  }


/* Make it responsive */
@media (max-width: 768px) {
    nav {
        flex-direction: column;
        align-items: flex-start;
    }

    ul {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
        list-style: none;
    }

    ul a {
        margin-bottom: 10px;
        font-size: 16px;
    }

    .searchBar {
        flex-direction: column;
        align-items: flex-start;
    }

    .searchBar input {
        width: 100%;
        margin-bottom: 10px;
    }

    .searchBar button {
        width: 100%;
    }

    .categoryBtn {
        flex-direction: column;
    }

    .categoryBtn button {
        margin-bottom: 10px;
    }
}